<template>
  <div class="blog-card">
    <div class="icons" v-show="editPost">
      <div class="icon" @click="editBlog">
        <img src="@/assets/Icons/edit-regular.svg" alt="edit" class="edit" />
      </div>
      <div class="icon" @click="deleteBlog">
        <img
          src="@/assets/Icons/trash-regular.svg"
          alt="delete"
          class="delete"
        />
      </div>
    </div>

    <img :src="post.blogPhotoFileURL" alt="cover" class="img-cover" />
    <div class="info">
      <h4>{{ post.blogTitle }}</h4>
      <h6>
        Posted on:
        {{
          new Date(post.blogDate).toLocaleString("en-us", { dateStyle: "long" })
        }}
      </h6>
      <router-link
        class="link"
        :to="{ name: 'blogView', params: { blogId: post.blogID } }"
        >View The Post
        <img src="@/assets/Icons/arrow-right-light.svg" alt="" class="arrow" />
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  props: ["post"],
  computed: {
    editPost() {
      return this.$store.state.editPost;
    },
  },

  methods: {
    deleteBlog() {
      if (confirm("Are you sure?")) {
        this.$store.dispatch("deletePost", this.post.blogID);
      }
    },
    editBlog() {
      this.$router.push({
        name: "editBlog",
        params: { blogId: this.post.blogID },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.blog-card {
  cursor: pointer;
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 8px;
  background-color: #fff;
  min-height: 420px;
  transition: 0.5s ease all;

  &:hover {
    transform: rotateZ(-1deg) scale(1.01);
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
      0 2px 4px -1px rgba(0, 0, 0, 0.06);
  }

  .icons {
    display: flex;
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 90;

    .icon {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 35px;
      height: 35px;
      border-radius: 50%;
      background-color: #fff;
      transition: 0.5s ease all;

      &:hover {
        background-color: #303030;
      }

      &:nth-child(1) {
        margin-right: 8px;
      }

      .edit,
      .delete {
        pointer-events: none;
        height: 15px;
        width: auto;
      }
    }
  }

  .img-cover {
    display: block;
    border-radius: 8px 8px 0 0;
    z-index: 1;
    width: 100%;
    min-height: 200px;
    object-fit: cover;
  }

  .info {
    display: flex;
    flex-direction: column;
    height: 100%;
    z-index: 3;
    padding: 32px 16px;
    color: #000;

    h4 {
      padding-bottom: 8px;
      font-size: 20px;
      font-weight: 300;
    }

    h6 {
      font-weight: 400;
      font-size: 12px;
      padding-bottom: 16px;
    }

    .link {
      display: inline-flex;
      align-items: center;
      margin-top: auto;
      font-weight: 500;
      padding-top: 20px;
      font-size: 12px;
      padding-bottom: 4px;
      transition: 0.5s ease-in all;

      &:hover {
        color: rgba(48, 48, 48, 0.8);
      }

      .arrow {
        width: 10px;
      }
    }
  }
}
</style>
