<template>
  <header>
    <nav class="container">
      <div class="branding">
        <router-link class="header" :to="{ name: 'home' }"
          >FireBlogs</router-link
        >
      </div>
      <div class="nav-links">
        <ul v-show="!mobile">
          <router-link class="link" to="/">Home</router-link>
          <router-link class="link" to="/blogs">Blogs</router-link>
          <router-link class="link" to="/create-post" v-if="user"
            >Create Post</router-link
          >
          <router-link class="link" to="/login" v-if="!user"
            >Login/Signup</router-link
          >
        </ul>
        <div
          class="profile"
          ref="profile"
          @click="toggleProfileMenu"
          v-if="user"
        >
          <span>{{ this.$store.state.profileInitials }}</span>
          <div class="profile-menu" v-show="profileMenu">
            <div class="info">
              <p class="initials">{{ this.$store.state.profileInitials }}</p>
              <div class="right">
                <p>
                  {{ this.$store.state.profileFirstName }}
                  {{ this.$store.state.profileLastName }}
                  <span v-if="admin">- Admin</span>
                </p>
                <p>{{ this.$store.state.profileUsername }}</p>
                <p>{{ this.$store.state.profileEmail }}</p>
              </div>
            </div>
            <div class="options">
              <div class="option">
                <router-link class="option" to="/profile">
                  <img src="@/assets/Icons/user-alt-light.svg" class="icon" />
                  <p>Profile</p>
                </router-link>
              </div>
              <div class="option" @click="signOut">
                <img
                  src="@/assets/Icons/sign-out-alt-regular.svg"
                  class="icon"
                />
                <p>Sign out</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </nav>
    <img
      src="@/assets/Icons/bars-regular.svg"
      class="menu-icon"
      @click="toggleMobileNav"
      v-show="mobile"
    />
    <!-- <menuIcon /> -->
    <transition name="mobile-nav">
      <ul class="mobile-nav" v-show="mobileNav">
        <router-link class="link" to="/">Home</router-link>
        <router-link class="link" to="/blogs">Blogs</router-link>
        <router-link class="link" to="/create-post" v-if="user"
          >Create Post</router-link
        >
        <router-link class="link" to="/login" v-if="!user"
          >Login/Signup</router-link
        >
      </ul>
    </transition>
  </header>
</template>

<script>
import firebase from "firebase/app";
import "firebase/auth";

export default {
  props: {},
  data() {
    return {
      mobile: null,
      mobileNav: null,
      windowWidth: null,
      profileMenu: null,
    };
  },
  created() {
    this.checkScreen();
    window.addEventListener("resize", this.checkScreen);
  },
  methods: {
    checkScreen() {
      this.windowWidth = window.innerWidth;
      if (this.windowWidth <= 750) {
        this.mobile = true;
        return;
      }
      this.mobile = false;
      this.mobileNav = false;
      return;
    },

    toggleMobileNav() {
      this.mobileNav = !this.mobileNav;
    },

    toggleProfileMenu(e) {
      if (e.target === this.$refs.profile) {
        this.profileMenu = !this.profileMenu;
      }
    },

    signOut() {
      firebase.auth().signOut();
      location.replace("/");
    },
  },

  computed: {
    user() {
      return this.$store.state.user;
    },
    admin() {
      return this.$store.state.admin;
    },
  },
};
</script>

